/* Home page cards*/
.go-corner {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  width: 32px;
  height: 32px;
  overflow: hidden;
  top: 0;
  right: 0;
  background-color:  #e8ded1;
  border-radius: 0 4px 0 32px;
}

.card {
  display: flex;
  position: relative;
  background-color: #f2f8f9;
  border-radius: 4px;
  padding: 15px 12px;
  margin: 12px;
  text-decoration: none;
  z-index: 0;
  overflow: hidden;
}
  
.card::before {
  content: "Hell";
  color: #e8ded1;
  position: "absolute";
  z-index: -10;
  top: -16px;
  right: -16px;
  background:  #e8ded1;
  height: 32px;
  width: 32px;
  border-radius: 32px;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  transition: -webkit-transform 0.25s ease-out;
  transition: transform 0.25s ease-out;
  transition: transform 0.25s ease-out, -webkit-transform 0.25s ease-out;
}

.card:hover:before {
  -webkit-transform: scale(30);
          transform: scale(30);
}

.img:hover{
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

